﻿import {
    Component,
    Input,
    Output,
    EventEmitter,
    ElementRef,
    Renderer,
    OnChanges,
    SimpleChanges,
    forwardRef, HostListener
} from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR, NG_VALIDATORS } from '@angular/forms';
import * as _ from 'lodash';
import { HttpClient, HttpParams } from '@angular/common/http';
import { finalize } from 'rxjs/operators';

export class NhSelectData {
    constructor(public id: any,
        public name: string,
        public icon?: string,
        public data?: any,
        public index?: number,
        public active?: boolean,
        public selected?: boolean) {
    }
}

@Component({
    selector: 'nh-select',
    templateUrl: './nh-select.component.html',
    providers: [
        { provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => NhSelectComponent), multi: true },
        { provide: NG_VALIDATORS, useExisting: forwardRef(() => NhSelectComponent), multi: true }
    ],
    styleUrls: ['./nh-select.scss']
})

export class NhSelectComponent implements OnChanges, ControlValueAccessor {
    private _data: NhSelectData[] = [];

    @Input() multiple = false;
    @Input() liveSearch: boolean;
    @Input() title: string;
    @Input() isEnable = true;
    @Input() width = 250;
    @Input() searchUrl = '';
    @Input() class = '';

    @Output() onSelectItem = new EventEmitter();
    @Output() valueChange = new EventEmitter();

    isSearching = false;
    isShowMenu = false;
    source = [];
    label;
    inputId;
    private _value: any;

    propagateChange: any = () => {
    }

    get value() {
        return this._value;
    }

    get data() {
        return this._data;
    }

    @Input()
    set data(values: NhSelectData[]) {
        setTimeout(() => {
            this._data = values;
            this.source = values.map((item, index) => {
                const obj = item;
                obj.index = index;
                obj.active = false;

                if (this.value && this.value instanceof Array) {
                    item.selected = this.value.indexOf(item.id) > -1;
                } else {
                    item.selected = item.id === this.value;
                }
                return obj;
            });

            const labelName = this.source.filter((item: any) => {
                return item.selected;
            }).map(item => item.name).join(',');

            if (labelName) {
                this.label = labelName;
            }
        });
    }

    @Input()
    set value(val) {
        if (val != null) {
            if (val instanceof Array) {
                this._value = val;
                const selectedItem = _.filter(this.source, (item) => {
                    return val.indexOf(item.id) > -1;
                });

                if (selectedItem && selectedItem.length > 0) {
                    _.each(selectedItem, (item) => {
                        item.selected = true;
                    });
                    this.label = this.getSelectedName(selectedItem);
                } else {
                    this.label = this.title;
                }
            } else {
                this.getSelectedItem(val);
            }
        } else {
            this.getSelectedItem(val);
        }
    }

    constructor(private http: HttpClient,
        private el: ElementRef, private renderer: Renderer) {
        this.inputId = `nh-select-${new Date().getTime() + Math.floor((Math.random() * 10) + 1)}`;
    }

    ngOnChanges(changes: SimpleChanges) {
    }

    searchKeyUp(e, term) {
        const keyCode = e.keyCode;
        // Navigate
        if (keyCode === 27) {
            this.isShowMenu = false;
        }

        if (keyCode === 27 || keyCode === 17 || e.ctrlKey) {
            return;
        }

        if (keyCode === 37 || keyCode === 38 || keyCode === 39 || keyCode === 40 || keyCode === 13) {
            this.navigate(keyCode);
            e.preventDefault();
        } else {
            if (!term) {
                this.source = this.data.map((item, index) => {
                    const obj = item;
                    obj.index = index;
                    obj.active = false;
                    obj.selected = false;
                    return obj;
                });
                return;
            }

            if (this.searchUrl) {
                this.source = [];
                this.isSearching = true;
                this.http.get<any>(this.searchUrl, {
                    params: new HttpParams().set('keyword', term)
                })
                    .pipe(finalize(() => this.isSearching = false))
                    .subscribe(result => {
                        this.source = result.map((item, index) => {
                            const obj = item;
                            obj.index = index;
                            obj.active = false;
                            obj.selected = false;
                            return obj;
                        });
                    });
            } else {
                const searchResult = _.filter(this.data, (item) => {
                    return this.stripToVietnameChar(item.name).indexOf(this.stripToVietnameChar(term)) > -1;
                });
                this.source = searchResult.map((item, index) => {
                    const obj = item;
                    obj.index = index;
                    obj.active = false;
                    obj.selected = false;
                    return obj;
                });
            }
        }
    }

    buttonClick() {
        this.isShowMenu = !this.isShowMenu;
        if (this.liveSearch && this.isShowMenu) {
            setTimeout(() => {
                this.renderer.invokeElementMethod(document.getElementById(this.inputId), 'focus');
            }, 200);
        }
    }

    selectItem(item: NhSelectData) {
        if (!this.multiple) {
            this.isShowMenu = false;
            this.label = item.name;
            _.each(this.source, (data) => {
                data.selected = false;
            });

            item.selected = true;
            this.value = item.id;
            this.propagateChange(item.id);
            this.onSelectItem.emit(item);
        } else {
            item.selected = !item.selected;
            const selectedItem = _.filter(this.source, (source: any) => {
                return source.selected;
            });
            this.label = selectedItem && selectedItem.length > 0 ? this.getSelectedName(selectedItem) : this.title;

            if (this.value instanceof Array) {
                const selectedIds = selectedItem.map((selected) => {
                    return selected.id;
                });
                this.onSelectItem.emit(selectedItem);
                this.propagateChange(selectedIds);
            } else {
                this.onSelectItem.emit(selectedItem);
                this.propagateChange(item.id);
            }
        }
    }

    @HostListener('document:click', ['$event'])
    onClick(el) {
        if (!this.el.nativeElement.contains(el.target)) {
            this.isShowMenu = false;
        }
    }

    stripToVietnameChar(str): string {
        str = str.toLowerCase();
        str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, 'a');
        str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, 'e');
        str = str.replace(/ì|í|ị|ỉ|ĩ/g, 'i');
        str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, 'o');
        str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, 'u');
        str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, 'y');
        str = str.replace(/đ/g, 'd');
        return str;
    }

    registerOnChange(fn) {
        this.propagateChange = fn;
    }

    writeValue(value) {
        if (value != null && value !== undefined) {
            this.value = value;
        } else {
            this.label = this.title;
        }
    }

    registerOnTouched() {
    }

    validate(c: FormControl) {
        this.value = c.value;
    }

    resetSelectedList() {
        _.each(this.source, (item) => {
            item.selected = false;
        });
        this.label = this.title;
    }

    private getSelectedItem(val) {
        _.each(this.source, (item) => {
            if (item.id === val) {
                this.label = item.name;
                item.selected = true;
            } else {
                item.selected = false;
            }
        });

        this._value = val;
        this.valueChange.emit(this._value);
    }

    private getSelectedName(listItem): string {
        return listItem.map((item) => {
            return item.name;
        }).join(', ');
    }

    private navigate(key: number) {
        const selectedItem = this.source.find((item) => {
            return item.active;
        });

        switch (key) {
            case 37:
                this.back(selectedItem);
                break;
            case 38:
                this.back(selectedItem);
                break;
            case 39:
                this.next(selectedItem);
                break;
            case 40:
                this.next(selectedItem);
                break;
            case 13:
                if (selectedItem) {
                    this.selectItem(selectedItem);
                }
                break;
        }
    }

    private next(selectedItem: any) {
        if (!selectedItem) {
            const firstItem = this.source[0];
            if (firstItem) {
                firstItem.active = true;
            }
        } else {
            let index = selectedItem.index + 1;
            if (index > this.source.length - 1) {
                index = 0;
            }

            const currentItem = this.source[index];
            this.resetActiveStatus();
            currentItem.active = true;
        }
    }

    private back(selectedItem: any) {
        if (!selectedItem) {
            const lastItem = this.source[this.source.length - 1];
            if (lastItem) {
                lastItem.active = true;
            }
        } else {
            let index = selectedItem.index - 1;
            if (index < 0) {
                index = this.source.length - 1;
            }

            const currentItem = this.source[index];
            this.resetActiveStatus();
            currentItem.active = true;
        }
    }

    private resetActiveStatus() {
        this.source.forEach((item) => item.active = false);
    }
}
