import {
    AfterContentInit, Component, ContentChild, ElementRef, EventEmitter, HostListener, Input, OnInit,
    Output,
    Renderer2,
    ViewEncapsulation
} from '@angular/core';
import { NhModalHeaderComponent } from './nh-modal-header.component';
import { NhModalFooterComponent } from './nh-modal-footer.component';

@Component({
    selector: 'nh-modal',
    template: `
        <div class="nh-modal-container"
             role="nh-modal-container"
             *ngIf="isShow"
             (click)="backdropClick($event)">
            <div [ngClass]="'nh-modal-dialog ' + size">
                <ng-content></ng-content>
            </div><!-- END: nh-modal-dialog -->
        </div><!-- END: nh-modal-container -->
    `,
    styleUrls: ['./nh-modal.component.scss'],
    encapsulation: ViewEncapsulation.None
    // animations: [
    //     trigger('showContainer', [
    //         state('show', style({opacity: 1})),
    //         state('hide', style({opacity: 0})),
    //         transition('* => show', animate('500ms')),
    //         transition('show => hide', animate('500ms'))
    //     ]),
    //     trigger('showEffect', [
    //         // BEGIN: Zoom effect
    //         state('zoomIn', style({transform: 'scale(1)'})),
    //         state('zoomOut', style({
    //             display: 'none',
    //             transform: 'scale(0.7)'
    //         })),
    //         transition('* => zoomIn', [
    //             animate(400, keyframes([
    //                 style({opacity: 0, transform: 'scale(0.5)', offset: 0}),
    //                 style({opacity: 1, transform: 'scale(1)', offset: 1.0})
    //             ]))
    //         ]),
    //         transition('zoomIn => zoomOut', [
    //             animate(300, keyframes([
    //                 style({opacity: 1, transform: 'scale(1)', offset: 0}),
    //                 style({opacity: 0, transform: 'scale(0.5)', offset: 1.0})
    //             ]))
    //         ]),
    //         // END: Zoom effect
    //         // BEGIN: Fade effect
    //         state('fadeIn', style({opacity: '1', display: 'block'})),
    //         state('fadeOut', style({opacity: '0', display: 'none'})),
    //         transition('* => fadeIn', animate('300ms ease-in')),
    //         transition('fadeIn => fadeOut', animate('300ms ease-out')),
    //         // END: Fade effect
    //         // BEGIN: SlideUp effect
    //         state('slideDownShow', style({transform: 'translateY(0)', display: 'block'})),
    //         state('slideDownHide', style({transform: 'translateY(-200px)', display: 'block', opacity: 0})),
    //         transition('* => slideDownShow', [
    //             animate(500, keyframes([
    //                 style({opacity: 0, transform: 'translateY(-200px)', offset: 0}),
    //                 style({opacity: 1, transform: 'translateY(30px)', offset: .5}),
    //                 style({opacity: 1, transform: 'translateY(-10px)', offset: .65}),
    //                 style({opacity: 1, transform: 'translateY(0)', offset: 1})
    //             ]))
    //         ]),
    //         transition('slideDownShow => slideDownHide', [
    //             animate(300, keyframes([
    //                 style({opacity: 1, transform: 'translateY(0)', offset: 0}),
    //                 style({opacity: 1, transform: 'translateY(50px)', offset: .7}),
    //                 style({opacity: 0, transform: 'translateY(-200px)', offset: 1})
    //             ]))
    //         ])
    //         // END: SlideUp effect
    //     ])
    // ]
})
export class NhModalComponent implements OnInit, AfterContentInit {
    @ContentChild(NhModalHeaderComponent) modalHeaderComponents: NhModalHeaderComponent;
    @ContentChild(NhModalFooterComponent) modalFooterComponents: NhModalFooterComponent;

    @Input() effect = 'slideDown'; // zoom, fade, slideUp, slideDown, slideLeft, slideRight
    @Input() size = 'full';
    @Input() backdropStatic = false;

    @Output() onShow = new EventEmitter();
    @Output() onShown = new EventEmitter();
    @Output() onHide = new EventEmitter();
    @Output() onHidden = new EventEmitter();
    @Output() onClose = new EventEmitter();

    isShow = false;
    state = '';
    hideState = ['zoomOut', 'fadeOut', 'slideUpHide', 'slideDownHide', 'slideLeftHide', 'slideRightHide'];
    showState = '';
    isDone = false;

    constructor(private el: ElementRef, private renderer: Renderer2) {
        this.changeState(true);
    }

    ngOnInit() {
    }

    ngAfterContentInit() {
        if (this.modalHeaderComponents) {
            this.modalHeaderComponents.onCloseModal.subscribe(() => {
                this.dismiss();
            });
        }

        if (this.modalFooterComponents) {
            this.modalFooterComponents.onCloseButtonClick.subscribe(() => {
                this.dismiss();
            });
        }
    }

    // @HostListener('document:click', ['$event'])
    backdropClick(e) {
        if (e.target.getAttribute('role') === 'nh-modal-container' && !this.backdropStatic) {
            this.dismiss();
        }
    }

    show() {
        this.renderer.setStyle(this.el.nativeElement, 'display', 'block');
        // this.showState = 'show';
        // setTimeout(() => {
        //     this.changeState();
        //     this.onShow.emit();
        // }, 100);
        // this.isDone = false;
        this.isShow = true;
        this.renderer.addClass(document.body, 'nh-modal-open');
        this.onShow.emit();
        this.onShown.emit();
    }

    dismiss() {
        this.renderer.removeClass(document.body, 'nh-modal-open');
        this.onHidden.emit();
        this.isShow = false;
    }

    showContainerDone(event) {
        if (event.fromState === 'hide' && event.toState === 'show') {
            // this.changeState();
            this.onShown.emit();
        }
        if (event.fromState === 'show' && event.toState === 'hide') {
            this.onHidden.emit();
        }
    }

    showEffectDone(event) {
        if (this.hideState.indexOf(this.state) > -1) {
            this.showState = 'hide';
            this.renderer.setStyle(this.el.nativeElement, 'display', 'none');
            this.onHidden.emit();
        }
        this.isDone = true;
    }

    private changeState(isHide = false) {
        switch (this.effect) {
            case 'zoom':
                this.state = isHide ? 'zoomOut' : this.state === 'zoomIn' ? 'zoomOut' : 'zoomIn';
                break;
            case 'fade':
                this.state = isHide ? 'fadeOut' : this.state === 'fadeIn' ? 'fadeOut' : 'fadeIn';
                break;
            case 'slideUp':
                this.state = isHide ? 'slideUpHide' : this.state === 'slideUpShow' ? 'slideUpHide' : 'slideUpShow';
                break;
            case 'slideDown':
                this.state = isHide ? 'slideDownHide' : this.state === 'slideDownShow' ? 'slideDownHide' : 'slideDownShow';
                break;
            case 'slideLeft':
                this.state = isHide ? 'slideLeftHide' : this.state === 'slideLeftShow' ? 'slideLeftHide' : 'slideLeftShow';
                break;
            case 'slideRight':
                this.state = isHide ? 'slideRightHide' : this.state === 'slideRightShow' ? 'slideRightHide' : 'slideRightShow';
                break;
            default:
                this.state = isHide ? 'zoomOut' : this.state === 'zoomIn' ? 'zoomOut' : 'zoomIn';
                break;
        }
    }
}
