import {
    AfterViewInit, Component, ContentChild, ElementRef, EventEmitter, Input, OnInit, Output,
    Renderer2
} from '@angular/core';
import { NhDismissDirective } from './nh-dismiss.directive';

@Component({
    selector: 'nh-modal-footer',
    template: `
        <ng-content></ng-content>
    `
})
export class NhModalFooterComponent implements OnInit, AfterViewInit {
    @ContentChild(NhDismissDirective) nhDismissDirective: NhDismissDirective;
    @Output() onCloseButtonClick = new EventEmitter();

    constructor(private ref: ElementRef, private renderer: Renderer2) {

    }

    ngOnInit() {

    }

    ngAfterViewInit() {
        if (this.nhDismissDirective) {
            this.nhDismissDirective.onClick.subscribe(x => {
                this.onCloseButtonClick.emit();
            });
        }
    }
}
