import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';

@Component({
    selector: 'nh-modal-header',
    template: `
        <ng-content></ng-content>
        <button class="close-button" *ngIf="showCloseButton" (click)="closeModal()">
            <i class="{{closeButtonIcon}}" *ngIf="closeButtonIcon; else defaultCloseButtonTemplate"></i>
            <ng-template #defaultCloseButtonTemplate>
                x
            </ng-template>
        </button>
    `,
    encapsulation: ViewEncapsulation.None
})
export class NhModalHeaderComponent implements OnInit {
    @Input() showCloseButton = true;
    @Input() closeButtonIcon = 'fa fa-times';
    @Output() onCloseModal = new EventEmitter();

    constructor() {
    }

    ngOnInit() {
    }

    closeModal() {
        this.onCloseModal.emit();
    }
}
